.App {
  text-align: center;
  font-family: Arial, sans-serif;
  color: #333;
  margin: 0;
  padding: 0;
}

.App-header {
  background-color: #00796b;
  color: white;
  padding: 20px;
}

.App-header h1 {
  margin: 0;
}

.main {
  padding: 20px;
}

.about, .services, .references, .contact {
  margin: 20px 0;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
}

h2 {
  color: #00796b;
}

.App-footer {
  background-color: #333;
  color: white;
  padding: 10px;
}
